<template>
  <div class="helpcenter">
    <div class="helpheader">
      <div class="helpnav">
        <ul class="helplist">
          <router-link :to="{path: '/helpcenter/guide'}">
            <li>操作指引</li>
          </router-link>
          <router-link :to="{path: '/helpcenter/common'}">
            <li>常见问题</li>
          </router-link>
<!--           <router-link :to="{path: '/helpcenter/back'}">
            <li>用户反馈</li>
          </router-link>
 -->        
        </ul>
      </div>
    </div>

  <keep-alive>
    <router-view></router-view>
  </keep-alive>
  
  </div>
</template>

<script type="text/ecmascript-6">
export default {}
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.helpcenter
  height 100%
  width 100%
  overflow-y hidden
.helpheader
  height 70px
  position fixed
  left 0px
  right 0px
  top 60px
  z-index 4
  background-color white
.helpnav
  max-width 700px
  height 70px
  margin 0 auto
  line-height 70px
.helplist
  display flex
.helplist li
  font-size 16px
  width 110px
  color #505050
  text-align left
  cursor pointer
  transition: color 0.2s 
.helplist li:hover
  color #00a1e9
  font-size 17px
.helplist .router-link-active li
  color rgba(0,161,233,0.79)
</style>



